import styled from 'styled-components';
import { Div, FixWidthContainer } from '../../theme/grid';
import { green, themeColor } from '../../theme/variables';
import { media } from '../../theme/media';

export const Line = styled(FixWidthContainer)`
  font-size: 1.2em;  
  padding: 15px;
  
  a {
    font-size: 1.3em;  
    cursor: pointer;
    text-decoration: none;
    line-height: 2em;
    color: ${themeColor};
    position: relative;
    display: inline-block;
    ${media.desktop`
      font-size: 17px;
    `}
  }
  
  span {
    position: relative;
    display: inline-block;
    &:hover { 
      &:after {
        transform: scaleX(1);
        transition: transform 0.3s ease-out;   
      } 
      
      i {
        color: ${green}
      } 
    }
    
    i {
      padding: 0 5px;
    }
  }
  
  span:after {
    position: absolute;
    content: "";
    height: 2px;
    width: 100%;
    bottom: -5px;
    left: 0;
    background: ${green};
    // transform-origin: 0%;
    transform: scaleX(0);
    transition: transform 0.3s ease-out;
  }
`;

export const ComponentContainer = styled(Div)`
  width: 100%; 
  text-align: center;
  padding-bottom: 15vh;
`;


